.Counter {
    display : -ms-flexbox;
    display : flex;
    text-align: center;
    padding: 1px;

}

.Counter button{
    border-radius: 60%;
    -webkit-transition-duration: 0.4s;
         -o-transition-duration: 0.4s;
            transition-duration: 0.4s;
    padding : 10px;
}

.Counter button:hover {
    background-color: #4CAF50; /* Green */
    color: white;
}


